@font-face {
  font-family: "HelveticaNeue";
  font-display: swap;
  font-weight: 400;
  font-style: normal;
  src: local("HelveticaNeue-Roman"), url("../fonts/HelveticaNeue-Roman.woff2") format("woff2"), url("../fonts/HelveticaNeue-Roman.woff") format("woff");
}
@font-face {
  font-family: "HelveticaNeue";
  font-display: swap;
  font-weight: 500;
  font-style: normal;
  src: local("HelveticaNeue-Medium"), url("../fonts/HelveticaNeue-Medium.woff2") format("woff2"), url("../fonts/HelveticaNeue-Medium.woff") format("woff");
}
@font-face {
  font-family: "Poppins";
  font-display: swap;
  font-weight: 400;
  font-style: normal;
  src: local("Poppins-Regular"), url("../fonts/Poppins-Regular.woff2") format("woff2"), url("../fonts/Poppins-Regular.woff") format("woff");
}
@font-face {
  font-family: "Poppins";
  font-display: swap;
  font-weight: 500;
  font-style: normal;
  src: local("Poppins-Medium"), url("../fonts/Poppins-Medium.woff2") format("woff2"), url("../fonts/Poppins-Medium.woff") format("woff");
}
@font-face {
  font-family: "Poppins";
  font-display: swap;
  font-weight: 600;
  font-style: normal;
  src: local("Poppins-SemiBold"), url("../fonts/Poppins-SemiBold.woff2") format("woff2"), url("../fonts/Poppins-SemiBold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  clip: rect(0, 0, 0, 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  font-size: 0;
}
.container {
  margin: 0 15px;
  padding: 0;
}
@media (min-width: 768px) {
  .container {
    margin: 0 30px;
    padding: 0;
  }
}
@keyframes linkhover {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}
.page {
  height: 100%;
  min-height: 100%;
  scroll-behavior: smooth;
  background-image: url("../img/desktop/bg-body.jpg");
}
.page__body {
  display: flex;
  flex-direction: column;
  max-width: 1260px;
  min-height: 100%;
  margin: 0 auto;
  padding: 0;
  font-family: "HelveticaNeue", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  background-color: #ffffff;
  color: #000000;
}
.header {
  margin: 0;
  padding: 0;
}
@media (min-width: 1260px) {
  .header {
    margin-bottom: 20px;
  }
}
.header__container {
  padding-top: 12px;
}
.header__top {
  position: relative;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding-bottom: 12px;
}
.header__top::before {
  content: "";
  position: absolute;
  top: auto;
  bottom: 0;
  left: -15px;
  width: calc(100% + 30px);
  height: 1px;
  background-color: #e8e8e8;
}
@media (min-width: 768px) {
  .header__top {
    left: -30px;
    width: calc(100% + 60px);
  }
}
@media (min-width: 1260px) {
  .header__top {
    justify-content: space-between;
  }
  .header__top::before {
    left: -30px;
    width: 1260px;
  }
}
.header__links {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin: 0 15px 0 auto;
  padding: 0;
}
@media (min-width: 768px) {
  .header__links {
    column-gap: 30px;
  }
}
.header__link {
  display: flex;
  font-family: "Poppins", "Arial", sans-serif;
  font-size: 12px;
  line-height: 18px;
  letter-spacing: 0.5px;
  color: #939393;
  transition: 0.3s ease;
}
.header__link:hover,
.header__link:focus {
  color: #9174ce;
}
.header__link:active {
  opacity: 0.7;
}
.header__link-text {
  margin: 0;
  padding: 0;
  position: relative;
  display: block;
}
.header__link-text::before {
  content: "";
  position: absolute;
  top: 3px;
  left: 0;
  background-repeat: no-repeat;
  transition: 0.3s ease;
}
.header__link-text--favorites {
  margin-right: 5px;
  padding-left: 20px;
}
