.header__main {
  display: flex;
  flex-wrap: wrap;
  padding-top: 25px;
}
@media (min-width: 1260px) {
  .header__main {
    align-items: center;
    margin-bottom: 20px;
  }
}
.header__menu {
  order: 1;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  gap: 15px;
  width: 100%;
  margin-bottom: 20px;
}
@media (min-width: 1260px) {
  .header__menu {
    order: 2;
    max-width: 242px;
    margin-bottom: 0;
  }
}
.header__menu-link {
  display: flex;
  gap: 10px;
  align-items: center;
  color: #000000;
}
.header__menu-link:hover,
.header__menu-link:focus {
  color: #9174ce;
}
.header__menu-link:active {
  opacity: 0.7;
}
.header__menu-text {
  display: block;
  font-size: 14px;
  line-height: 18px;
  font-weight: 600;
  transition: 0.3s ease;
  cursor: pointer;
}
.header__menu-text--account {
  display: none;
}
@media (min-width: 768px) {
  .header__menu-text--account {
    display: block;
  }
}
.header__menu-text--cart {
  text-transform: uppercase;
}
.header__menu-icon {
  cursor: pointer;
}
.header__menu-icon--stroke {
  stroke: #000000;
  stroke-width: 0.04px;
}
.header__menu-link:hover .header__menu-icon--stroke,
.header__menu-link:hover .header__menu-icon--stroke-cart {
  transition: 0.3s ease;
  stroke: #9174ce;
}
.header__menu-icon--fill {
  fill: none;
}
.header__menu-icon--stroke-cart {
  stroke: #000000;
  stroke-width: 0.09px;
}
.fil0 {
  fill: none;
}
.contacts {
  margin: 0;
  padding: 0;
}
.contacts--header {
  display: none;
}
@media (min-width: 1260px) {
  .contacts--header {
    display: block;
  }
}
@media (min-width: 1260px) {
  .contacts--footer {
    max-width: 365px;
    padding: 0 15px;
  }
}
.contacts__text {
  font-family: "Poppins", "Arial", sans-serif;
  font-size: 12px;
  line-height: 20px;
  letter-spacing: 1px;
  color: #939393;
}
.contacts__text--link:hover {
  color: #9174ce;
}
.contacts__address {
  font-style: normal;
}
@media (min-width: 1260px) {
  .contacts__address {
    max-width: 260px;
  }
}
.contacts__phone-link {
  transition: 0.3s ease;
}
.contacts__phone-link:hover,
.contacts__phone-link:focus {
  color: #9174ce;
}
.contacts__phone-link:active {
  opacity: 0.7;
}
.contacts__list {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
}
.contacts__inner {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 10px;
}
.contacts__img-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
}
.contacts__icon {
  display: block;
  width: 25px;
  height: 30px;
}
.logo {
  order: 0;
  width: 100%;
  margin: 0 0 15px 0;
  padding: 0;
}
@media (min-width: 1260px) {
  .logo {
    max-width: 177px;
    margin-right: 180px;
    margin-bottom: 0;
  }
}
.logo__link {
  display: block;
  margin: 0 auto;
  padding: 0;
  max-width: 177px;
}
.logo__img {
  display: block;
  width: 100%;
  height: 100;
  object-fit: cover;
}
.form {
  flex-grow: 1;
  order: 2;
  margin: 0;
  padding: 0;
}
@media (min-width: 1260px) {
  .form {
    order: 1;
    max-width: 481px;
    margin-right: 120px;
  }
}
.form__search {
  display: flex;
  width: 100%;
}
.form__input {
  flex-grow: 1;
  margin: 0;
  padding: 9px 16px;
  border: 2px solid #9174ce;
  border-radius: 5px 0 0 5px;
  outline: none;
  transition: 0.3s ease;
}
.form__input::placeholder {
  font-size: 12px;
  line-height: 1.33;
  letter-spacing: 0.7px;
  color: #7c7c7c;
}
.form__input:hover,
.form__input:focus {
  opacity: 0.8;
}
.form__input--subscribe {
  border: 0;
  background-color: #f5f5f5;
}
.button {
  display: block;
  max-width: 103px;
  margin: 0;
  padding: 0;
  font-family: "Poppins", "Arial", sans-serif;
  background-color: #9174ce;
  border: 0;
  border-radius: 5px;
  cursor: pointer;
}
.button__search {
  width: 50px;
  height: 40px;
  padding: 7px 10px;
  background-image: url("data:image/svg+xml,%3Csvg width='17' height='17' viewBox='0 0 17 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M13.458 12.044L17 15.586L15.586 17L12.044 13.458C10.782 14.421 9.21 15 7.5 15C3.35786 15 0 11.6421 0 7.5C0 3.35786 3.35786 0 7.5 0C11.6421 0 15 3.35786 15 7.5C15.0001 9.14309 14.4581 10.7403 13.458 12.044ZM7.5 2C4.467 2 2 4.467 2 7.5C2.00331 10.5362 4.4638 12.9967 7.5 13C10.532 13 13 10.532 13 7.5C13 4.467 10.532 2 7.5 2Z' fill='white'/%3E%3C/svg%3E%0A");
  background-repeat: no-repeat;
  background-position: center center;
  border-radius: 0 5px 5px 0;
  transition: 0.3s ease;
}
.button__search:hover,
.button__search:focus {
  opacity: 0.9;
}
.button__tab {
  max-width: 100%;
  margin: 0 auto;
  padding: 6px 17px;
  font-size: 13px;
  line-height: 1.4;
  font-weight: 600;
  background-color: transparent;
  transition: 0.3s ease;
  letter-spacing: 1px;
}
.button__tab:hover,
.button__tab:focus {
  color: #9174ce;
}
.button__tab:active {
  opacity: 0.7;
}
.button__subscribe {
  padding: 10px 7px;
  border-radius: 0 5px 5px 0;
  transition: 0.3s ease;
}
.button__subscribe:hover {
  background-color: #000000;
  border-color: #000000;
}
.button__text {
  color: #ffffff;
  font-weight: 500;
  text-transform: uppercase;
}
.products__swiper--show.button__tab {
  color: #9174ce;
}
.main__title {
  max-width: 200px;
  margin: 0 0 20px 0;
  padding: 0;
  font-size: 16px;
  line-height: 20px;
  font-weight: 600;
  text-transform: uppercase;
  text-align: center;
  word-break: break-all;
}
@media (min-width: 768px) {
  .main__title--products {
    display: flex;
    align-items: center;
    margin-bottom: -60px;
  }
}
.main__title--specials {
  max-width: 100px;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: -23px;
  text-align: center;
}
@media (min-width: 768px) {
  .main__title--specials {
    display: flex;
    align-items: center;
    max-width: 200px;
    margin-bottom: -40px;
  }
}
.main__title--blog {
  text-align: left;
}
.main-nav {
  position: relative;
  margin: 10px 0 0 0;
  padding: 0;
}
@media (min-width: 1260px) {
  .main-nav {
    padding: 10px 0;
  }
  .main-nav::before,
  .main-nav::after {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: #e8e8e8;
  }
  .main-nav::before {
    top: 0;
  }
  .main-nav::after {
    bottom: 0;
  }
}
.main-nav__button {
  display: block;
  margin: 0;
  padding: 0;
  border: 0;
  background-color: transparent;
  cursor: pointer;
}
.main-nav__button-toggle {
  display: none;
}
.main-nav__button-submenu {
  display: none;
}
.main-nav__list {
  margin: 0;
  padding: 5px;
  list-style: none;
  transition: 0.5s ease;
}
@media (min-width: 1260px) {
  .main-nav__list {
    display: flex;
    flex-wrap: wrap;
    padding: 0;
  }
}
.main-nav__item {
  position: relative;
  transition: 0.4s linear;
}
@media (min-width: 1260px) {
  .main-nav__item:hover {
    background-color: #9174ce;
    border-radius: 5px;
  }
  .main-nav__item:hover::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    animation-name: linkhover;
    animation-duration: 0.4s;
    background-color: #9b83e6;
    opacity: 0.4;
  }
}
@media (min-width: 1260px) {
  .main-nav__item:hover .main-nav__link {
    color: #ffffff;
  }
}
.main-nav__link,
.main-nav__text {
  position: relative;
  display: block;
  padding: 10px;
  font-family: "Poppins", "Arial", sans-serif;
  font-size: 12px;
  font-weight: 500;
  letter-spacing: 1px;
  color: #000000;
  transition: 0.3s linear;
}
.main-nav__link:hover,
.main-nav__text:hover,
.main-nav__link:focus,
.main-nav__text:focus {
  color: #9174ce;
}
.main-nav__link:active,
.main-nav__text:active {
  opacity: 0.7;
}
.main-nav__link {
  position: relative;
}
