  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 1;
  grid-row-end: 2;
  min-height: 133px;
}
.banner__img-link {
  display: block;
  width: 100%;
  height: 100%;
}
.banner__img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
