  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 1;
  grid-row-end: 2;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
  width: 50%;
  height: min-content;
  margin-right: auto;
  padding-top: 50px;
  padding-left: 20px;
}
.banner__content--main-slider {
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-end;
  margin-left: auto;
  margin-right: 0;
