.advantages__inner {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 56px;
  height: 56px;
  background-color: #9174ce;
  border-radius: 50%;
  transition: 0.5s ease;
}
.advantages__card:hover .advantages__inner {
  background-color: #000000;
}
.advantages__img {
  display: block;
  transition: 0.7s ease;
}
.advantages__card:hover .advantages__img {
  transform: rotate(360deg);
}
.advantages__title {
  margin: 0;
  padding: 0;
  font-size: 12px;
  line-height: 22px;
  font-weight: 600;
  letter-spacing: 0.6px;
  text-transform: uppercase;
}
.advantages__description {
  margin: 0;
  padding: 0;
  font-family: "Poppins", "Arial", sans-serif;
  color: #434343;
  font-size: 12px;
  line-height: 20px;
  font-weight: 300;
  letter-spacing: 1px;
}
